"use client"
import { useApp } from "@/lib/context/app-context"
import { useTranslation } from "@/lib/hooks/use-translation"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Textarea } from "@/components/ui/textarea"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Badge } from "@/components/ui/badge"
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, PieChart, Pie, Cell } from "recharts"
import { BottomNav } from "@/components/bottom-nav"
import { useState, useEffect } from "react"
import { PlusCircle, Wallet, TrendingUp, TrendingDown, AlertTriangle, Target, Calendar } from "lucide-react"
import { toast } from "@/hooks/use-toast"

const COLORS = ["#059669", "#10b981", "#34d399", "#6ee7b7", "#d1fae5", "#ecfdf5"]

// Import budget service
import { 
  getBudgetOverview, 
  addExpense, 
  getCategories,
  formatCurrency,
  formatDate,
  getCategoryColor,
  getBudgetHealthStatus,
  type BudgetOverview, 
  type CreateExpenseRequest, 
  type BudgetCategory 
} from "@/lib/services/budget-service"

export default function BudgetPage() {
  const { state } = useApp()
  const t = useTranslation()

  // State management
  const [budgetData, setBudgetData] = useState<BudgetOverview | null>(null)
  const [categories, setCategories] = useState<BudgetCategory[]>([])
  const [loading, setLoading] = useState(true)
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [error, setError] = useState<string | null>(null)

  // Form state
  const [newExpense, setNewExpense] = useState({
    amount: "",
    category: "",
    description: "",
    notes: ""
  })

  // Fetch data on component mount
  useEffect(() => {
    fetchData()
  }, [])

  const fetchData = async () => {
    try {
      setLoading(true)
      setError(null)
      
      // Fetch budget overview and categories
      const [overview, categoriesData] = await Promise.all([
        getBudgetOverview(),
        getCategories()
      ])
      
      setBudgetData(overview)
      setCategories(categoriesData)

    } catch (error) {
      console.error('Error fetching budget data:', error)
      setError('Failed to fetch budget data. Please try again.')
      toast({
        title: "Error",
        description: "Failed to fetch budget data. Please try again.",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  // Calculate spending data for charts
  const categoryData = budgetData?.categories.map((cat) => ({
    id: cat.categoryId,
    name: state.settings.language === "ar" ? cat.nameAr : cat.name,
    spent: cat.spent,
    limit: cat.limit,
    percentage: cat.percentage || 0,
  })) || []

  const pieData = categoryData.filter(cat => cat.spent > 0).map((cat) => ({
    name: cat.name,
    value: cat.spent,
  }))

  const budget = budgetData?.budget
  const totalSpent = budget?.spent || 0
  const totalBudget = budget?.total || 0
  const budgetUtilization = budget?.percentage || 0

  // Handle form submission
  const handleSubmitExpense = async () => {
    try {
      setIsSubmitting(true)
      
      if (!newExpense.amount || !newExpense.category || !newExpense.description) {
        toast({
          title: "Validation Error",
          description: "Please fill in all required fields.",
          variant: "destructive",
        })
        return
      }

      // Create expense using budget service
      const expenseData: CreateExpenseRequest = {
        amount: parseFloat(newExpense.amount),
        category: newExpense.category,
        description: newExpense.description,
        notes: newExpense.notes,
        expense_date: new Date().toISOString(),
        payment_method: 'Other'
      }

      await addExpense(expenseData)

      // Reset form
      setNewExpense({ amount: "", category: "", description: "", notes: "" })
      setIsDialogOpen(false)

      // Refresh data
      await fetchData()

      toast({
        title: "Success",
        description: "Expense recorded successfully!",
        variant: "default",
      })

    } catch (error) {
      console.error('Error creating expense:', error)
      toast({
        title: "Error",
        description: "Failed to record expense. Please try again.",
        variant: "destructive",
      })
    } finally {
      setIsSubmitting(false)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-background pb-24 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-emerald-600 mx-auto mb-4"></div>
          <p className="text-muted-foreground">Loading budget data...</p>
        </div>
      </div>
    )
  }

  if (error && !budgetData) {
    return (
      <div className="min-h-screen bg-background pb-24 flex items-center justify-center">
        <div className="text-center max-w-md mx-auto">
          <AlertTriangle className="h-12 w-12 text-red-500 mx-auto mb-4" />
          <h2 className="text-lg font-semibold mb-2">Error Loading Budget</h2>
          <p className="text-muted-foreground mb-4">{error}</p>
          <Button onClick={fetchData} className="bg-emerald-600 hover:bg-emerald-700">
            Try Again
          </Button>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background pb-24 flex justify-center">
      <main className="container max-w-4xl py-6 px-4">
        {/* Header */}
        <div className="mb-6 flex justify-between items-start">
          <div>
            <h1 className="text-3xl font-bold text-foreground">
              {state.settings.language === "ar" ? "متتبع الميزانية" : "Budget Tracker"}
            </h1>
            <p className="text-muted-foreground mt-2">
              {state.settings.language === "ar"
                ? "تابع نفقاتك الشهرية وتحقق من أهدافك المالية"
                : "Track your monthly spending and reach your financial goals"}
            </p>
          </div>

          {/* Add Expense Button */}
          <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
            <DialogTrigger asChild>
              <Button className="bg-emerald-600 hover:bg-emerald-700">
                <PlusCircle className="h-4 w-4 mr-2" />
                {state.settings.language === "ar" ? "إضافة مصروف" : "Add Expense"}
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
              <DialogHeader>
                <DialogTitle>
                  {state.settings.language === "ar" ? "إضافة مصروف جديد" : "Add New Expense"}
                </DialogTitle>
                <DialogDescription>
                  {state.settings.language === "ar" 
                    ? "أدخل تفاصيل المصروف لتتبعه في ميزانيتك"
                    : "Enter expense details to track it in your budget"}
                </DialogDescription>
              </DialogHeader>
              <div className="grid gap-4 py-4">
                <div className="grid grid-cols-4 items-center gap-4">
                  <Label htmlFor="amount" className="text-right">
                    {state.settings.language === "ar" ? "المبلغ" : "Amount"}
                  </Label>
                  <Input
                    id="amount"
                    type="number"
                    step="0.01"
                    placeholder="100.00"
                    className="col-span-3"
                    value={newExpense.amount}
                    onChange={(e) => setNewExpense(prev => ({ ...prev, amount: e.target.value }))}
                  />
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <Label htmlFor="category" className="text-right">
                    {state.settings.language === "ar" ? "الفئة" : "Category"}
                  </Label>
                  <Select value={newExpense.category} onValueChange={(value) => setNewExpense(prev => ({ ...prev, category: value }))}>
                    <SelectTrigger className="col-span-3">
                      <SelectValue placeholder="Select category" />
                    </SelectTrigger>
                    <SelectContent>
                      {categories.map((cat) => (
                        <SelectItem key={cat.categoryId} value={cat.categoryId}>
                          {state.settings.language === "ar" ? cat.nameAr : cat.name}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <Label htmlFor="description" className="text-right">
                    {state.settings.language === "ar" ? "الوصف" : "Description"}
                  </Label>
                  <Input
                    id="description"
                    placeholder="e.g., Lunch at restaurant"
                    className="col-span-3"
                    value={newExpense.description}
                    onChange={(e) => setNewExpense(prev => ({ ...prev, description: e.target.value }))}
                  />
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <Label htmlFor="notes" className="text-right">
                    {state.settings.language === "ar" ? "ملاحظات" : "Notes"}
                  </Label>
                  <Textarea
                    id="notes"
                    placeholder="Additional notes (optional)"
                    className="col-span-3"
                    rows={3}
                    value={newExpense.notes}
                    onChange={(e) => setNewExpense(prev => ({ ...prev, notes: e.target.value }))}
                  />
                </div>
              </div>
              <DialogFooter>
                <Button 
                  type="submit" 
                  onClick={handleSubmitExpense}
                  disabled={isSubmitting}
                  className="bg-emerald-600 hover:bg-emerald-700"
                >
                  {isSubmitting ? "Recording..." : (state.settings.language === "ar" ? "حفظ المصروف" : "Record Expense")}
                </Button>
              </DialogFooter>
            </DialogContent>
          </Dialog>
        </div>

        {/* Overall Budget Summary */}
        <Card className="mb-6 border-emerald-200 bg-linear-to-br from-emerald-50 to-white dark:from-emerald-950 dark:to-background">
          <CardHeader>
            <CardTitle className="text-emerald-900 dark:text-emerald-100 flex items-center">
              <Wallet className="h-5 w-5 mr-2" />
              {state.settings.language === "ar" ? "ملخص الميزانية الشهرية" : "Monthly Budget Summary"}
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-3 gap-4 mb-6">
              <div>
                <p className="text-sm text-muted-foreground">
                  {state.settings.language === "ar" ? "الميزانية الكلية" : "Total Budget"}
                </p>
                <p className="text-2xl font-bold text-emerald-700 dark:text-emerald-400">
                  {formatCurrency(totalBudget)}
                </p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground flex items-center">
                  <TrendingDown className="h-4 w-4 mr-1" />
                  {state.settings.language === "ar" ? "المبلغ المنفق" : "Spent"}
                </p>
                <p className="text-2xl font-bold text-orange-600 dark:text-orange-400">
                  {formatCurrency(totalSpent)}
                </p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground flex items-center">
                  <TrendingUp className="h-4 w-4 mr-1" />
                  {state.settings.language === "ar" ? "المتبقي" : "Remaining"}
                </p>
                <p className="text-2xl font-bold text-green-600 dark:text-green-400">
                  {formatCurrency(totalBudget - totalSpent)}
                </p>
              </div>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between text-sm mb-1">
                <span className="text-muted-foreground">
                  {state.settings.language === "ar" ? "استخدام الميزانية" : "Budget Utilization"}
                </span>
                <span className="font-semibold">{budgetUtilization.toFixed(1)}%</span>
              </div>
              <Progress value={budgetUtilization} className="h-3" />
            </div>
          </CardContent>
        </Card>

        {/* Category Breakdown */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
          {/* Bar Chart */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">
                {state.settings.language === "ar" ? "الإنفاق حسب الفئة" : "Spending by Category"}
              </CardTitle>
            </CardHeader>
            <CardContent>
              {categoryData.some(cat => cat.spent > 0) ? (
                <ResponsiveContainer width="100%" height={300}>
                  <BarChart data={categoryData}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="name" tick={{ fontSize: 12 }} angle={-45} textAnchor="end" height={80} />
                    <YAxis />
                    <Tooltip formatter={(value) => `${value} SAR`} />
                    <Bar dataKey="spent" fill="#059669" name="Spent" />
                    <Bar dataKey="limit" fill="#d1fae5" name="Limit" />
                  </BarChart>
                </ResponsiveContainer>
              ) : (
                <div className="h-[300px] flex items-center justify-center text-muted-foreground">
                  <div className="text-center">
                    <p>No expenses recorded yet</p>
                    <p className="text-sm mt-1">Add your first expense to see the chart</p>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>

          {/* Pie Chart */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">
                {state.settings.language === "ar" ? "توزيع النفقات" : "Expense Distribution"}
              </CardTitle>
            </CardHeader>
            <CardContent>
              {pieData.length > 0 ? (
                <ResponsiveContainer width="100%" height={300}>
                  <PieChart>
                    <Pie
                      data={pieData}
                      cx="50%"
                      cy="50%"
                      labelLine={false}
                      label={({ name, percent }) => `${name}: ${((percent || 0) * 100).toFixed(0)}%`}
                      outerRadius={100}
                      fill="#8884d8"
                      dataKey="value"
                    >
                      {pieData.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                      ))}
                    </Pie>
                    <Tooltip formatter={(value) => `${value} SAR`} />
                  </PieChart>
                </ResponsiveContainer>
              ) : (
                <div className="h-[300px] flex items-center justify-center text-muted-foreground">
                  <div className="text-center">
                    <p>No expenses to display</p>
                    <p className="text-sm mt-1">Record expenses to see distribution</p>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Category Details */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle className="text-lg">
              {state.settings.language === "ar" ? "تفاصيل الفئات" : "Category Details"}
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {categoryData.map((category) => {
                const isOverBudget = category.percentage > 100
                return (
                  <div key={category.id} className="border-b pb-4 last:border-0">
                    <div className="flex justify-between items-center mb-2">
                      <div>
                        <h3 className="font-semibold">{category.name}</h3>
                        <p className="text-sm text-muted-foreground">
                          {category.spent.toFixed(2)} / {category.limit} SAR
                        </p>
                      </div>
                      <div className="text-right">
                        <p className={`font-bold ${isOverBudget ? "text-red-600" : "text-emerald-600"}`}>
                          {category.percentage.toFixed(0)}%
                        </p>
                        {isOverBudget && (
                          <p className="text-xs text-red-500">Over budget!</p>
                        )}
                      </div>
                    </div>
                    <Progress value={Math.min(category.percentage, 100)} className="h-2" />
                  </div>
                )
              })}
            </div>
          </CardContent>
        </Card>

        {/* Recent Expenses */}
        {expenses.length > 0 && (
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">
                {state.settings.language === "ar" ? "المصروفات الأخيرة" : "Recent Expenses"}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                {expenses.slice(0, 10).map((expense) => {
                  const category = BUDGET_CATEGORIES.find(cat => cat.id === expense.category)
                  const categoryName = category ? 
                    (state.settings.language === "ar" ? category.nameAr : category.name) 
                    : expense.category
                  
                  return (
                    <div key={expense.id} className="flex justify-between items-center p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                      <div>
                        <p className="font-medium">{expense.description}</p>
                        <p className="text-sm text-muted-foreground">{categoryName}</p>
                        <p className="text-xs text-muted-foreground">
                          {new Date(expense.date).toLocaleDateString()}
                        </p>
                      </div>
                      <div className="text-right">
                        <p className="font-bold text-red-600">-{expense.amount.toFixed(2)} SAR</p>
                      </div>
                    </div>
                  )
                })}
              </div>
            </CardContent>
          </Card>
        )}
      </main>

      <BottomNav />
    </div>
  )
}
