import { apiClient, isApiSuccess } from "../api/client"

export interface BudgetCategory {
  categoryId: string;
  name: string;
  nameAr: string;
  limit: number;
  spent: number;
  remaining?: number;
  percentage?: number;
  isOverBudget?: boolean;
  recentExpenses?: BudgetExpense[];
}

export interface BudgetExpense {
  _id?: string;
  amount: number;
  category: string;
  description: string;
  notes?: string;
  expense_date: string;
  payment_method?: string;
  tags?: string[];
  is_recurring?: boolean;
  recurring_frequency?: string;
}

export interface Budget {
  id: string;
  total: number;
  spent: number;
  remaining: number;
  percentage: number;
  daysRemaining: number;
  period: string;
  startDate: string;
  endDate: string;
  currency: string;
}

export interface BudgetOverview {
  budget: Budget;
  categories: BudgetCategory[];
  recentExpenses: BudgetExpense[];
  monthlyTrend: Array<{
    month: string;
    amount: number;
  }>;
  alerts: {
    isOverBudget: boolean;
    isNearLimit: boolean;
    overBudgetCategories: number;
  };
}

export interface CreateExpenseRequest {
  amount: number;
  category: string;
  description: string;
  notes?: string;
  expense_date?: string;
  payment_method?: string;
  tags?: string[];
  is_recurring?: boolean;
  recurring_frequency?: string;
}

export interface UpdateBudgetRequest {
  total_budget?: number;
  budget_period?: string;
  categories?: Array<{
    categoryId: string;
    limit: number;
  }>;
  alerts?: {
    enabled: boolean;
    threshold_percentage: number;
    category_alerts: boolean;
  };
}

// Get budget overview
export async function getBudgetOverview(): Promise<BudgetOverview> {
  const response = await apiClient.get<BudgetOverview>('/budget/overview');
  
  if (isApiSuccess(response)) {
    return response.data;
  }
  
  throw new Error(response.error || 'Failed to fetch budget overview');
}

// Get budget
export async function getBudget(): Promise<any> {
  const response = await apiClient.get<any>('/budget');
  
  if (isApiSuccess(response)) {
    return response.data;
  }
  
  throw new Error(response.error || 'Failed to fetch budget');
}

// Add expense
export async function addExpense(expense: CreateExpenseRequest): Promise<BudgetExpense> {
  const response = await apiClient.post<BudgetExpense>('/budget/expenses', expense);
  
  if (isApiSuccess(response)) {
    return response.data;
  }
  
  throw new Error(response.error || 'Failed to add expense');
}

// Get budget categories
export async function getCategories(): Promise<BudgetCategory[]> {
  const response = await apiClient.get<BudgetCategory[]>('/budget/categories');
  
  if (isApiSuccess(response)) {
    return response.data;
  }
  
  throw new Error(response.error || 'Failed to fetch categories');
}

// Update budget
export async function updateBudget(data: UpdateBudgetRequest): Promise<any> {
  const response = await apiClient.put<any>('/budget', data);
  
  if (isApiSuccess(response)) {
    return response.data;
  }
  
  throw new Error(response.error || 'Failed to update budget');
}

// Helper functions
export function formatCurrency(amount: number, currency: string = 'SAR'): string {
  return new Intl.NumberFormat('ar-SA', {
    style: 'currency',
    currency: currency,
    minimumFractionDigits: 0,
    maximumFractionDigits: 2,
  }).format(amount);
}

export function formatDate(date: string | Date): string {
  const dateObj = typeof date === 'string' ? new Date(date) : date;
  return new Intl.DateTimeFormat('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric'
  }).format(dateObj);
}

export function getCategoryColor(categoryId: string): string {
  const colorMap: Record<string, string> = {
    'coffee': '#8B4513',
    'dining': '#FF6347',
    'entertainment': '#9370DB',
    'groceries': '#32CD32',
    'fuel': '#FFD700',
    'pharmacy': '#20B2AA',
    'shopping': '#FF69B4',
    'utilities': '#4169E1',
    'other': '#808080'
  };
  return colorMap[categoryId] || '#808080';
}

export function getBudgetHealthStatus(percentage: number): 'good' | 'warning' | 'danger' {
  if (percentage <= 70) return 'good';
  if (percentage <= 90) return 'warning';
  return 'danger';
}